/* 保养库的列表title */
export function setServerColumns({
  handleEdit, handleDelete, type
}) {
  return [
    {
      title: '设备类型',
      dataIndex: 'deviceType',
      key: 'deviceType'
    },
    {
      title: type === 'repair' ? '点检类型' : '保养类型',
      dataIndex: 'contentType',
      key: 'contentType',
      render: value =>
        value === 1 ? '是否型' : value === 2 ? '选项型' : '输入型'
    },
    {
      title: type === 'repair' ? '点检内容' : '保养内容',
      dataIndex: 'content',
      key: 'content'
    },
    {
      title: '创建时间',
      dataIndex: 'createTime',
      key: 'createTime'
    },
    {
      title: '创建人',
      dataIndex: 'creater',
      key: 'creater'
    },
    {
      title: '操作',
      dataIndex: 'maintainId',
      key: 'maintainId',
      align: 'right',
      render: value => {
        return (
          <span style={{ color: 'var(--PC)', cursor: 'pointer' }}>
            <span onClick={() => handleEdit(value)}>编辑</span>
            <Divider type='vertical' />
            <span onClick={() => handleDelete({ value, type: 'single' })}>
              删除
            </span>
          </span>
        );
      }
    }
  ];
}
