/* 设置form-columns */
export function setFormColumns({
  type, status, currentMain, handleRadio, contentType, onAddItem, onDelItem, handleDataValue, minValue, maxValue
}) {
  return [
    {
      key: 'content',
      label: type === 'repair' ? '点检内容' : '保养内容',
      value: <Input placeholder='请输入' style={{ width: '330px' }} />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.content : undefined,
        rules: [
          {
            required: true,
            message: '请输入内容'
          }
        ]
      }
    },
    {
      key: 'contentType',
      label: type === 'repair' ? '点检类型' : '保养类型',
      value: (
        <Radio.Group onChange={handleRadio}>
          <Radio value={1}>是否型</Radio>
          <Radio value={2}>选项型</Radio>
          <Radio value={3}>输入型</Radio>
        </Radio.Group>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.contentType : 1,
        rules: [
          {
            required: true,
            message: '请选择类型'
          }
        ]
      }
    },
    {
      key: 'extendBtn',
      label: <div />,
      displayNone: contentType === 1,
      colon: false,
      value: (
        <div>
          {contentType === 2 ? (
            [
              <Button type='primary' onClick={onAddItem}>
                增加选项
              </Button>,
              <Button style={{ marginLeft: '5px' }} onClick={onDelItem}>
                删除选项
              </Button>
            ]
          ) : (
              <div>
                <span>请输入数据区间值</span>
                <div>
                  最小值：
                <Input
                    style={{ width: '200px' }}
                    value={minValue}
                    onChange={(e) => handleDataValue('minValue', e)}
                  />
                </div>
                <div>
                  最大值：
                <Input
                    style={{ width: '200px' }}
                    value={maxValue}
                    onChange={(e) => handleDataValue('maxValue', e)}
                  />
                </div>
              </div>
            )}
        </div>
      )
    }
  ];
}

