/* 设置保养计划弹窗-columns */
export function setPlanModalColumns({
  status, currentMain, deviceByIdList, schemaByIdList
}) {
  return [
    {
      key: 'planName',
      label: '计划名称',
      value: <Input placeholder='请输入' style={{ width: '250px' }} />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.planName : undefined,
        rules: [
          {
            required: true,
            message: '必填'
          }
        ]
      }
    },
    {
      key: 'deviceId',
      label: '选择设备',
      value: (
        <Select
          showSearch allowClear
          style={{ width: 250, }}
          placeholder='选择设备'
        >
          {
            deviceByIdList && deviceByIdList.map(item => {
              return <Option key={item.id} value={item.id}>
                {item.devName}
              </Option>
            })
          }
        </Select>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? Number(currentMain.deviceId) : undefined,
        rules: [
          {
            required: true,
            message: '必填'
          }
        ]
      }
    },
    {
      key: 'schemaId',
      label: '选择方案',
      value: (
        <Select
          showSearch allowClear
          style={{ width: 250, }}
          placeholder='选择方案'
        >
          {
            schemaByIdList && schemaByIdList.map(item => {
              return <Option key={item.id} value={item.id}>
                {item.planName}
              </Option>
            })
          }
        </Select>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.schemaId : undefined,
        rules: [
          {
            required: true,
            message: '必填'
          }
        ]
      }
    },
    {
      key: 'excuteTime',
      label: '计划执行时间',
      value: <DatePicker
        mode='time' showTime
        format="HH:mm:ss"
        locale={locale}
        placeholder="请选择时间"
        style={{ width: '250px', display: 'inline-block' }}
      />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? moment(currentMain.excuteTime) : undefined,
        rules: [
          {
            required: true,
            message: '必填'
          }
        ]
      }
    },
    {
      key: 'planWaitTime',
      label: '计划等待时间',
      value: <TimeInput />,
      getFieldDecorator: {
        initialValue: status === 'edit' ?
          { waitTime: currentMain.waitTime, excuteUnit: currentMain.excuteUnit } : undefined,
        rules: [
          {
            required: true,
            message: '等待时间大于0'
          }
        ]
      }
    },
    {
      key: 'excuteCycle',
      label: '执行周期',
      value: <Select
        showSearch allowClear
        style={{ width: 250, }}
        placeholder='选择'
      >
        <Option value={1}>天</Option>
        <Option value={2}>周</Option>
      </Select>,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.excuteCycle : 1,
        rules: [
          {
            required: true,
            message: '必填'
          }
        ]
      }
    },
  ]
}

