import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import './index.less';
import GlobalList from 'components/GlobalList';
import { Modal, Input, Button, message } from 'antd';
import { setServerColumns, setFormColumns } from './methods';
import ModalS from './cell/ModalS';

@inject('ZCloudDeviceStore')
@observer
class CheckServer extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.ZCloudDeviceStore;
		this.state = {
			selectedRowKeys: [],
			visible: false,
			contentType: 1,
			maxValue: '',
			minValue: '',
			extendFormColumns: []
		};
	}
	render() {
		const {
			maintainList,
			maintainPage,
			deviceList,
			isLoading,
			status,
			currentMain
		} = this.store;
		const { contentType, minValue, maxValue } = this.state;
		const rowSelection = {
			selectedRowKeys: this.state.selectedRowKeys,
			onChange: this.handleSelect
		};
		const modal = {
			visible: this.state.visible,
			title: status === 'add' ? '新增' : '编辑',
			onCancel: this.handleVisible,
			onOk: this.handleSubmit,
			isForm: true,
			okText: '确定',
			cancelText: '取消',
			formColumns: setFormColumns({
				status, currentMain, type: this.props.type,
				handleRadio: this.handleRadio,
				contentType, minValue, maxValue,
				onAddItem: this.onAddItem,
				onDelItem: this.onDelItem,
				handleDataValue: this.handleDataValue,
			}),
			extendFormColumns: this.state.contentType === 2 && this.state.extendFormColumns
		}
		return (
			<div>
				<GlobalList
					checkedTag={{
						dataSource: deviceList,
						title: '设备类型',
						PopoVisible: false,
						keyName: 'typeName',
						defaultActiveKey: deviceList[0],
						content: deviceList,
						onClick: this.handleCheckTag
					}}
					table={{
						rowKey: 'maintainId',
						columns: setServerColumns({
							type: this.props.type, handleEdit: this.handleEdit, handleDelete: this.handleDelete
						}),
						dataSource: maintainList,
						pagination: {
							...maintainPage,
							onChange: (pageIndex, pageSize) => {
								this.store.getMaintain({
									pageIndex,
									pageSize
								});
							}
						},
						loading: isLoading,
						rowSelection,
						btns: [
							{
								name: '新增',
								onClick: () => {
									if (this.store.deviceType) {
										this.store.status = 'add';
										this.handleVisible('add');
									} else {
										message.warn('请选择设备类型')
									}
								},
								type: 'primary'
							},
							{
								name: '删除',
								onClick: () => this.handleDelete({ type: 'batch' })
							}
						]
					}}

				/>
				{
					this.state.visible && <ModalS
						modal={modal}
					/>
				}
			</div>
		);
	}
	/* modal - operate */
	handleVisible = (status) => {
		if (this.state.visible) {
			this.state.extendFormColumns = []
			this.state.minValue = ''
			this.state.maxValue = ''
		}
		if (status === 'add') {
			this.state.contentType = 1
		}
		this.setState({
			visible: !this.state.visible
		});
	};
	handleRadio = e => {
		this.setState({
			contentType: e.target.value
		});
	};
	onDelItem = () => {
		let extendsCol = this.state.extendFormColumns;
		extendsCol.pop();
		this.setState({
			extendFormColumns: extendsCol
		});
	};
	onAddItem = () => {
		let extendsCol = this.state.extendFormColumns;
		extendsCol.push({
			id: extendsCol.length + 1,
			label: '选项',
			value: <Input placeholder='请输入' style={{ width: '330px' }} />,
			key: 'strValue',
			getFieldDecorator: {
				// initialValue:item || undefined,
				rules: [
					{
						required: true,
						message: '必填'
					}
				]
			}
		});
		this.setState({
			extendFormColumns: extendsCol
		});
	};
	handleDataValue = (type, e) => {
		this.setState({
			[type]: e.target.value
		});
	};
	handleSubmit = values => {
		const { deviceType } = this.store;
		let success = true;
		let strValue = [],
			params = {
				deviceType,
				maintainType: this.store.maintainType
			};
		for (let k in values) {
			if (k.indexOf('strValue') > -1) {
				strValue.push(values[k]);
			} else {
				params[k] = values[k];
			}
		}
		if (values.contentType === 2) {
			if (strValue.length < 1) {
				message.error('请输入至少一条选项值');
				success = false;
				return;
			}
			params.strValue = strValue.join(';');
		}
		if (values.contentType === 3) {
			params.minValue = parseFloat(this.state.minValue);
			params.maxValue = parseFloat(this.state.maxValue);
		}
		if (this.store.status === 'edit') {
			params.maintainId = this.store.currentMain.maintainId
		}
		this.store.saveMaintain(params);
		if (success) {
			this.handleVisible();
		}
	};
	/* table - operate */
	handleEdit = value => {
		this.store.status = 'edit';
		this.store.getServeInfo({ id: value }).then(() => {
			let extList = [];
			if (this.store.currentMain.contentType === 2) {
				let strValue = this.store.currentMain.strValue.split(';');
				strValue.map((item, idx) => {
					extList.push({
						id: idx + 1,
						label: '选项',
						value: <Input placeholder='请输入' style={{ width: '330px' }} />,
						key: 'strValue',
						getFieldDecorator: {
							initialValue: item || undefined,
							rules: [
								{
									required: true,
									message: '必填'
								}
							]
						}
					})
				})
			} else if (this.store.currentMain.contentType === 3) {
				this.state.minValue = this.store.currentMain.minValue;
				this.state.maxValue = this.store.currentMain.maxValue;
			}
			this.setState({
				contentType: this.store.currentMain.contentType,
				extendFormColumns: extList
			})
			this.handleVisible();
		});
	};
	handleDelete = ({ value, type }) => {
		if (type === 'batch') {
			if (this.state.selectedRowKeys.length < 1) {
				Modal.warn({
					title: '提示',
					content: '请选择需要删除的内容'
				});
			} else {
				Modal.confirm({
					title: '提示',
					content:
						'确定删除选中的' +
						this.state.selectedRowKeys.length +
						'条内容？',
					okText: '确定',
					cancelText: '取消',
					onOk: () => {
						this.store.removeMaintain(
							{
								ids: this.state.selectedRowKeys
							},
							'batch'
						);
					}
				});
			}
		} else {
			Modal.confirm({
				title: '提示',
				content: '确定删除当前内容？',
				okText: '确定',
				cancelText: '取消',
				onOk: () => {
					this.store.removeMaintain(
						{
							id: value
						},
						'single'
					);
				}
			});
		}
	};
	handleSelect = selectedRowKeys => {
		this.setState({ selectedRowKeys });
	};
	/* check - tag */
	handleCheckTag = tag => {
		this.store.deviceType = tag.typeName;
		this.store.getMaintain({
			pageIndex: 1,
			pageSize: 10,
			deviceType: tag.typeName === '全部' ? '' : tag.typeName
		});
	};
	componentDidMount() {
		this.store.deviceType = ''
		this.store.getMaintain({
			pageIndex: 1,
			pageSize: 10,
			deviceType: this.store.deviceType
		});
	}
}

export default CheckServer;
