import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import './index.less';
import GlobalList from 'components/GlobalList';
import { Table, } from 'antd';
import { isEmpty } from 'lodash';
import { setOperateColumns, setModalOperateColumns } from './methods';
import { toJS } from 'mobx';
import ModalS from './cell/ModalS'

@inject('ZCloudDeviceStore')
@observer
class MaintainOperate extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.ZCloudDeviceStore;
		this.state = {
			selectedRowKeys: [],
			visible: false,
			contents: {}
		};
	}
	render() {
		const {
			operateList,
			operatePage,
			op_deviceList,
			listByContentsIds,
			isLoading,
		} = this.store;
		const { type } = this.props;
		const modal = {
			width: 700,
			visible: this.state.visible,
			title: '编辑',
			onCancel: this.handleVisible,
			onOk: this.handleSubmit,
			okText: '确定',
			cancelText: '取消',
			children: !isEmpty(this.state.contents) && <Table
				size='small'
				dataSource={listByContentsIds}
				columns={setModalOperateColumns({
					type,
					onRadioChange: this.onRadioChange,
					onCheckBoxChange: this.onCheckBoxChange,
					onInputNumber: this.onInputNumber,
					onDescription: this.onDescription,
					contents: this.state.contents
				})}
				pagination={false}
			/>
		}
		return (
			<div>
				<GlobalList
					checkedTag={{
						dataSource: op_deviceList,
						title: '设备名称',
						PopoVisible: false,
						keyName: 'devName',
						defaultActiveKey: op_deviceList[0],
						content: op_deviceList,
						onClick: this.handleCheckTag
					}}
					table={{
						rowKey: 'operateID',
						columns: setOperateColumns({
							type, handleEdit: this.handleEdit
						}),
						dataSource: operateList,
						pagination: {
							...operatePage,
							onChange: (pageIndex, pageSize) => {
								this.store.getOperateList({
									pageIndex,
									pageSize
								});
							}
						},
						loading: isLoading,
					}}
				/>
				{
					this.state.visible && <ModalS
						modal={modal}
					/>
				}
			</div>
		);
	}
	/* modal - operate */
	handleVisible = () => {
		this.setState({
			visible: !this.state.visible
		});
	};
	handleSubmit = () => {
		const { currentMain: { id, schemaId, contentIds, } } = toJS(this.store);
		const { contents } = this.state;

		let params = {
			planId: id, schemaId, contentIds,
			number: contentIds.split(',').length,
		}
		Object.keys(contents).map((key, index) => {
			const { maintainId, contentType, remark } = contents[key];
			if (parseFloat(key) === maintainId) {
				params[`maintainID${index}`] = maintainId
				params[`contentType${index}`] = contentType
				params[`remark${index}`] = remark
				if (contentType === 1) {
					params[`boolValue${index}`] = contents[key].boolValue
				}
				if (contentType === 2) {
					params[`strValue${index}`] = contents[key].strValue.join(',')
				}
				if (contentType === 3) {
					params[`minValue${index}`] = contents[key].minValue
					params[`maxValue${index}`] = contents[key].maxValue
					params[`doubleValue${index}`] = contents[key].doubleValue
				}
			}
		})
		this.store.saveExcution(params);
		this.handleVisible();
	};
	onDescription = (record, value) => {
		let { maintainId } = record;
		this.setState({
			contents: {
				...this.state.contents,
				[maintainId]: {
					...this.state.contents[maintainId],
					remark: value
				}
			}
		})
	}
	onCheckBoxChange = (record, value) => {
		let { maintainId } = record;
		this.setState({
			contents: {
				...this.state.contents,
				[maintainId]: {
					...this.state.contents[maintainId],
					strValue: value
				}
			}
		})
	}
	onRadioChange = (record, value) => {
		let { maintainId } = record;
		this.setState({
			contents: {
				...this.state.contents,
				[maintainId]: {
					...this.state.contents[maintainId],
					boolValue: value
				}
			}
		})
	}
	onInputNumber = (record, value) => {
		let { maintainId } = record;
		this.setState({
			contents: {
				...this.state.contents,
				[maintainId]: {
					...this.state.contents[maintainId],
					doubleValue: value
				}
			}
		})
	}
	/* table - operate */
	handleEdit = (contentIds, record) => {

		this.store.status = 'edit';
		this.store.currentMain = record;
		this.store.getListByContents({ contentIds: record.contentIds, devId: this.store.devId }).then((data) => {
			// let data = this.store.listByContentsIds;
			let contents = {};
			data && data.map(item => {
				contents[item.maintainId] = []
				/* 设置所有数据的初始化 */
				const { maintainId, contentType, minValue, maxValue } = item;
				let obj = {
					maintainId, contentType, remark: ''
				}
				if (contentType === 1) {
					obj.boolValue = 1
				}
				if (contentType === 2) {
					obj.strValue = [];
					obj.strList = []
					item.strValue.split(';').map(item => {
						item && obj.strList.push({
							label: item, value: item
						})
					})
				}
				if (contentType === 3) {
					obj = {
						...obj,
						minValue, maxValue, doubleValue: 0
					}
				}
				contents[item.maintainId] = obj
			})
			this.setState({
				contents
			})
		});
		this.handleVisible();
	};

	/* check - tag */
	handleCheckTag = tag => {
		console.log(tag, 'tag')
		this.store.deviceId = tag.id;
		this.store.devId = tag.devId;
		this.store.getOperateList({
			deviceId: tag.id
		});
	};
	componentDidMount() {
		this.store.deviceId = ''
		this.store.getOperateDevice().then(() => {
			this.store.getOperateList({
				deviceId: this.store.deviceId
			})
		})

	}
}
export default MaintainOperate;
