import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import './index.less';
import GlobalList from 'components/GlobalList';
import { Modal, message } from 'antd';
import { setPlanColumns, setPlanModalColumns } from './methods';
import { omit } from 'lodash'

@inject('ZCloudDeviceStore')
@observer
class CheckPlan extends Component {
    constructor(props) {
        super(props);
        this.store = this.props.ZCloudDeviceStore;
        this.state = {
            selectedRowKeys: [],
            visible: false,
        };

    }
    render() {
        const {
            checkPlanList, checkPlanPage,
            deviceList, deviceByIdList,
            isLoading,
            status,
            currentMain,
            schemaByIdList
        } = this.store;

        const rowSelection = {
            selectedRowKeys: this.state.selectedRowKeys,
            onChange: this.handleSelect
        };
        return (
            <div>
                <GlobalList
                    checkedTag={{
                        dataSource: deviceList,
                        title: '设备类型',
                        PopoVisible: false,
                        keyName: 'typeName',
                        defaultActiveKey: deviceList[0],
                        content: deviceList,
                        onClick: this.handleCheckTag
                    }}
                    table={{
                        rowKey: 'id',
                        columns: setPlanColumns({
                            handleEdit: this.handleEdit, handleDelete: this.handleDelete,
                            handleToDetail: this.handleToDetail
                        }),
                        dataSource: checkPlanList,
                        pagination: {
                            ...checkPlanPage,
                            onChange: (pageIndex, pageSize) => {
                                this.store.getPlan({
                                    pageIndex,
                                    pageSize
                                });
                            }
                        },
                        loading: isLoading,
                        rowSelection,
                        btns: [
                            {
                                name: '创建计划',
                                onClick: () => {
                                    this.store.status = 'add';
                                    this.handleVisible();
                                },
                                type: 'primary'
                            },
                            {
                                name: '删除',
                                onClick: () => this.handleDelete({ type: 'batch' })
                            }
                        ]
                    }}
                    modal={{
                        visible: this.state.visible,
                        title: status === 'add' ? '创建计划' : '编辑',
                        onCancel: () => this.setState({ visible: !this.state.visible }),
                        onOk: this.handleSubmit,
                        isForm: true,
                        formColumns: setPlanModalColumns({
                            status, currentMain, deviceByIdList, schemaByIdList
                        }),
                    }}
                />
            </div>
        );
    }
    /* modal - operate */
    handleSubmit = values => {
        const { deviceType, status, currentMain } = this.store;
        let params = {
            planType: this.store.planType, excuteStatus: 1,
            typeId: deviceType,
            maintainType: this.store.maintainType,
            ...omit(values, 'planWaitTime'),
            ...values.planWaitTime,
            excuteTime: window.GLOBAL_PARAMS.moment(values.excuteTime).format('HH:MM:SS')
        }
        if (status === 'edit') {
            params.id = currentMain.id
        }
        this.store.savePlan(params);
        this.setState({
            visible: !this.state.visible
        });
    };
    handleVisible = () => {
        if (!this.state.visible) {
            if (this.store.deviceType) {
                this.store.getListByType('device', { typeId: this.store.deviceType });
                this.store.getListByType('schema');
                this.setState({
                    visible: !this.state.visible
                });
            } else {
                message.warning('请选择设备类型')
            }

        }

    };
    /* table - operate */
    handleEdit = value => {
        this.store.status = 'edit';
        this.store.getPlanInfo({ id: value }).then((excuteTime) => {
            this.store.currentMain.excuteTime = window.GLOBAL_PARAMS.moment().format('YYYY-MM-DD') + ' ' + excuteTime
            this.store.getListByType('device', { typeId: this.store.currentMain.typeId });
            this.store.getListByType('schema', { typeId: this.store.currentMain.typeId });
        });

        this.setState({
            visible: !this.state.visible
        });
    };
    handleDelete = ({ value, type }) => {
        if (type === 'batch') {
            if (this.state.selectedRowKeys.length < 1) {
                Modal.warn({
                    title: '提示',
                    content: '请选择需要删除的计划'
                });
            } else {
                Modal.confirm({
                    title: '提示',
                    content:
                        '确定删除选中的' +
                        this.state.selectedRowKeys.length +
                        '条计划？',
                    okText: '确定',
                    cancelText: '取消',
                    onOk: () => {
                        this.store.delPlan(
                            {
                                ids: this.state.selectedRowKeys
                            },
                            'batch'
                        );
                    }
                });
            }
        } else {
            Modal.confirm({
                title: '提示',
                content: '确定删除当前计划？',
                okText: '确定',
                cancelText: '取消',
                onOk: () => {
                    this.store.delPlan(
                        {
                            id: value
                        },
                        'single'
                    );
                }
            });
        }
    };
    handleToDetail = record => {
        Modal.confirm({
            title: '提示',
            content: '确定下发当前点检计划？',
            okText: '确定',
            cancelText: '取消',
            onOk: () => {
                this.store.toDetail(
                    {
                        id: record.id
                    }
                );
            }
        })
    }
    handleSelect = selectedRowKeys => {
        this.setState({ selectedRowKeys });
    };
    /* check - tag */
    handleCheckTag = tag => {
        this.store.deviceType = tag.typeId;
        this.store.getPlan({
            typeId: tag.typeId
        });
    };
    componentDidMount() {
        this.store.getDeviceList();
        this.store.deviceType = this.store.deviceList[0].typeId
        this.store.getPlan({
            typeId: this.store.deviceList[0].typeId
        });
    }
}
export default CheckPlan;