import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import './index.less';
import GlobalList from 'components/GlobalList';
import { Modal, Table, message } from 'antd';
import ModalS from './cell/ModalS'
import { setSchemaColumns, setSchemaFormColumns, setSchemaModalColumns, } from './methods';

@inject('ZCloudDeviceStore')
@observer
class CheckScheme extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.ZCloudDeviceStore;
		this.state = {
			selectedRowKeys: [],
			modalSelectedRowKeys: [], //弹窗内部的选项
			contentIds: [], //弹窗内部的选项
			visible: false,
		};
	}
	render() {
		const {
			schemeList,
			schemePage,
			maintainList,
			deviceList,
			isLoading,
			status,
			currentMain
		} = this.store;
		const { type } = this.props;
		const rowSelection = {
			selectedRowKeys: this.state.selectedRowKeys,
			onChange: this.handleSelect
		};
		const modal = {
			visible: this.state.visible,
			title: status === 'add' ? '新增' : '编辑',
			onCancel: this.handleVisible,
			onOk: this.handleSubmit,
			isForm: true,
			okText: '确定',
			cancelText: '取消',
			formColumns: setSchemaFormColumns({
				status, currentMain, type
			}),
			otherContent: <div>
				<Table
					scroll={{ y: 300 }}
					rowKey='maintainID'
					columns={setSchemaModalColumns({ type })}
					dataSource={maintainList}
					rowSelection={{
						selectedRowKeys: this.state.modalSelectedRowKeys,
						onChange: this.onModalItemSelect
					}}
					pagination={false}
					size='small'
				/>
			</div>
		}
		return (
			<div>
				<GlobalList
					checkedTag={{
						dataSource: deviceList,
						title: '设备类型',
						PopoVisible: false,
						keyName: 'typeName',
						defaultActiveKey: deviceList[0],
						content: deviceList,
						onClick: this.handleCheckTag
					}}
					table={{
						rowKey: 'id',
						columns: setSchemaColumns({
							type, handleDelete: this.handleDelete, handleEdit: this.handleEdit
						}),
						dataSource: schemeList,
						pagination: {
							...schemePage,
							onChange: (pageIndex, pageSize) => {
								this.store.getScheme({
									pageIndex,
									pageSize,
									planType: 2,
								});
							}
						},
						loading: isLoading,
						rowSelection,
						btns: [
							{
								name: '新增',
								onClick: () => {
									if (this.store.deviceType) {
										this.store.status = 'add';
										this.handleVisible();
									} else {
										message.warn('请选择设备类型')
									}
								},
								type: 'primary'
							},
							{
								name: '删除',
								onClick: () => this.handleDelete({ type: 'batch' })
							}
						]
					}}
				/>
				{
					this.state.visible && <ModalS
						modal={modal}
					/>
				}
			</div>
		);
	}
	/* modal - operate */
	handleVisible = () => {
		/* 每次打开弹窗 获取所有的点检列表 */
		if (this.state.visible) {
			this.store.getMaintain({
				pageSize: 100
			});
			this.setState({
				modalSelectedRowKeys: [],
				contentIds: []
			})
		}
		this.setState({
			visible: !this.state.visible
		});
	};
	/* 弹窗内表格的操作 */
	onModalItemSelect = (selectedRowKey, selectedRowKeys) => {
		// console.log(selectedRowKeys)
		let arr = []
		selectedRowKeys.map(item => {
			arr.push(item.maintainId)
		})
		this.setState({
			modalSelectedRowKeys: selectedRowKey,
			contentIds: arr
		})
	}
	/* 弹窗提交操作 */
	handleSubmit = values => {
		console.log(values, '提交')
		const { deviceType, status, currentMain } = this.store;
		let params = {
			deviceType,
			contentIds: this.state.contentIds.join(','),
			planType: this.store.planType,
			maintainType: this.store.maintainType,
			...values
		}
		if (status === 'edit') {
			params.id = currentMain.id
		}
		this.store.saveSchema(params);
		this.handleVisible();
	};
	/* 编辑弹窗 */
	handleEdit = value => {
		this.store.status = 'edit';
		this.store.getSchemaInfo({ id: value }).then(() => {
			let data = this.store.currentMain.contentIds.split(','), newDs = [];
			data.map(item => {
				newDs.push(parseFloat(item))
			})
			this.setState({
				modalSelectedRowKeys: newDs
			})
		})
		this.handleVisible();
	};
	/* 删除 点检方案 */
	handleDelete = ({ type, value }) => {
		if (type === 'batch') {
			if (this.state.selectedRowKeys.length < 1) {
				Modal.warn({
					title: '提示',
					content: '请选择需要删除的内容'
				});
			} else {
				Modal.confirm({
					title: '提示',
					content:
						'确定删除选中的' +
						this.state.selectedRowKeys.length +
						'条内容？',
					okText: '确定',
					cancelText: '取消',
					onOk: () => {
						this.store.delSchema(
							{
								ids: this.state.selectedRowKeys
							},
							'batch'
						);
					}
				});
			}
		} else {
			Modal.confirm({
				title: '提示',
				content: '确定删除当前内容？',
				okText: '确定',
				cancelText: '取消',
				onOk: () => {
					this.store.delSchema(
						{
							id: value
						},
						'single'
					);
				}
			});
		}
	};
	handleSelect = selectedRowKeys => {
		this.setState({ selectedRowKeys });
	};
	/* check - tag */
	handleCheckTag = tag => {
		console.log(tag.typeName, 'name')
		this.store.deviceType = tag.typeName
		this.store.getMaintain({
			pageIndex: 1,
			pageSize: 10,
			deviceType: tag.typeName === '全部' ? '' : tag.typeName
		});
		this.store.getScheme({
			deviceType: tag.typeName === '全部' ? '' : tag.typeName,
		});
	};
	componentDidMount() {
		this.store.deviceType = ''
		this.store.getMaintain({
			pageIndex: 1,
			pageSize: 10,
		});
		this.store.getScheme({
			deviceType: this.store.deviceType,
		});
	}
}
export default CheckScheme;
